<template>
  <div>
    <div class="special-html-content">
     
 
      <!-- CSS样式 -->
      <!-- 内容 ----------------------------------------------->
      <div class="nr-main">
        <!-- 顶部图片 ------------------------------->
        <div class="nr-img"></div>
        <!-- 顶部图片 ------------------------------->
        <!-- 文字部分 ------------------------------->
        <div class="nr-text">
          <div class="nr-text-main">
           
            <div class="text-sub-w">
              <div class="text-sub-z">
                <div class="text-sub-n">{{title}}</div>
              </div>
              <div class="sub-line"></div>
              <!--横线 -->
            </div>
            <div style="text-align: left; margin-bottom: 15px" v-html="detailData">
              
            </div>
           
            <!-- （申请方式） ----------------->
          </div>
        </div>
        <!-- 文字部分 ------------------------------->
      </div>
      <!-- 内容 ----------------------------------------------->
    </div>
  </div>
</template>

<script>
import { getContent } from "../api/public";
export default {
  data() {
    return {
      detailData: '',
      id: '',
      typeName:'',
      title: ''
    }
  },
 
  mounted() {
    this.id = this.$route.query.id
    this.typeName = this.$route.query.typeName
    this.title = this.$route.query.title
    this.getDetail()
    document.title = this.title
  },
  methods: {
    getDetail() {
     
      getContent({
        "typeName": this.typeName,
        "id": Number(this.id)
      }).then(res=>{
        this.detailData= res
      })
    },
   
  }
}
</script>

<style>
.nr-main {
          text-align: center;
          position: relative;
          overflow: hidden;
          color: #5b5b5b;
          font-family: Arial, "微软雅黑", "黑体";
          font-size: 16px;
          line-height: 32px;
        }
        .nr-img {
         
         
          position: relative;
        }
        .nr-text {
          background-image: url("https://img.112695.com/img/ef7aa10a57464595a2df842ae7414d95.jpg");
          padding-top: 50px;
          padding-bottom: 50px;
        }
        .nr-text-main {
          margin: 0 auto;
          width: 940px;
          border: 1px solid #f0f0f0;
          padding: 10px 60px 0px 60px;
          background-color: #ffffff;
        }
        .text-red {
          color: #b60022;
        }
        .text-sub-w {
          position: relative;
          width: 940px;
          height: 52px;
          margin-bottom: 20px;
          margin-top: 30px;
        }
        .text-sub-z {
          text-align: center;
          position: absolute;
          top: 0px;
          z-index: 100;
          width: 940px;
          height: 52px;
        }
        .text-sub-n {
          margin: 0 auto;
          width: 250px;
          height: 52px;
          border-radius: 26px;
          background-color: #b60022;
          color: #ffffff;
          font-size: 18px;
          line-height: 50px;
          font-weight: bold;
        }
        .sub-line {
          position: absolute;
          top: 26px;
          z-index: 90;
          width: 100%;
          height: 1px;
          background-color: #f0f0f0;
        }
        .tab {
          border-collapse: separate;
          border-spacing: 1px;
          text-align: center;
          font-size: 18px;
        }
        .tab th {
          width: 225px;
          height: 60px;
          background: #b60022;
          color: #fff;
          font-size: 18px;
        }
        .tab tr {
          height: 60px;
          background: #efefef;
          color: #5b5b5b;
          font-size: 18px;
        }
        .tab td {
          height: 60px;
          background: #efefef;
          color: #5b5b5b;
          font-size: 18px;
        }
</style>